            this.$axios({
              url:""+this.url_api+"/v1/common/role/remove/"+id,
              method: "post",
              // params:{},
              // data:{},
              //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{
                console.log(res)
                if(res.data.message==="success"){
                  this.part();
                  this.masking=false;
                }
                  // location.reload(); // 
              })
              .catch(error=>{
                console.log(error);
              })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });


        },
        //
        preserve(){
          console.log('');
          if ( this.status==""){
            this.status=1
          }else if(this.status==""){
            this.status=0
          };
          console.log("");
          console.log(this.creator)
          this.$axios({
            url:""+this.url_api+"/v1/common/role/update/"+this.id,
            method: "post",
            // params:{},
            data:{
              name:this.part_name,
              code:this.code,
              desc:this.desc,
              desc_en:this.desc_en,
              status:this.status,
              hotel_id:this.grogshop_id,
              role_group:this.role_group,

            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                // location.reload();  //
                this.part();
                this.masking=false;

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })




        },
        //
        saveInfo_role(){
          let that=this;
          if(that.normal=="0"){
            that.normal=0
          }else if(that.normal=="1"){
            that.normal=1
          }


          that.$axios({
            "url":  ""+that.url_api+"/v1/common/role/add",
            "method": "post",
            data:{
              name:that.name,//
              role_group:that.role_group,//
              code:that.code,//
              hotel_id:that.hotel_id,//id
              status:that.normal,//
              desc:that.desc,//
              desc_en:that.desc_en,//
            },
            //"headers": {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                // console.log(res);
                that.part();
              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });
        },


         /*
         * 
         * */
         //
        saveInfo_rower(){
          let that=this;
          if(that.normal=="0"){
            that.normal=0
          }else if(that.normal=="1"){
            that.normal=1
          }
          that.$axios({
            "url": ""+that.url_api+"/v1/common/rule/add",
            "method": "post",
            data:{
              name:that.name,//
              rule_group :that.rule_groups ,//
              code:that.code,//
              status:that.normal,//
              desc:that.desc,//
              desc_en:that.desc_en,//
            },
            // "headers": {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res);
                that.power();
              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });

        },

        //
        revision(srow,id,index,list){
          this.masking=true;
          this.right_control=true;
          this.role=false
          console.log(srow);
           this.permission_name=srow.name;//
            this.api=srow.url;//API
            this.status_bar=srow.status;//
              if(srow.status===0){
                this.status_bar=""
              }else  if(srow.status===1){
                this.status_bar=""
              }
            this.rule_groups=srow.rule_group.name
            this.permission_name_id=srow.id;//id
            this.chinese_name=srow.desc;//
            this.english_name=srow.desc_en;//
            // this.associated=;//

        },

        //
        cance(id,index,list){
          console.log(id);
          console.log(index);
          console.log(list);
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
            list.splice(index, 1);
            this.$axios({
              url:""+this.url_api+"/v1/common/rule/remove/"+id,
              method: "post",
              // params:{},
              // data:{},
              //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{
                console.log(res)
                if(res.data.message==="success"){
                  this.power();
                }
                // location.reload(); // 
              })
              .catch(error=>{
                console.log(error);
              })



          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });

        },

        //
        keep(){
          if (this.status_bar==""){
            this.status_bar=1
          }else if(this.status_bar==""){
            this.status_bar=0
          };

          console.log("");
          console.log(this.creator)
          this.$axios({
            url:""+this.url_api+"/v1/common/rule/update/"+this.permission_name_id,
            method: "post",
            // params:{},
            data:{
              name:this.permission_name,
              desc:this.chinese_name,
              desc_en:this.english_name,
              status: this.status_bar,
              url:this.api,
              rule_group:this.rule_groups,

            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                // location.reload();  //
                this.power();
                this.masking=false;
                console.log(this.permission_name);

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })

        },





        //
        compile_power(id,index){
          let that = this;
          console.log(id);
          that.toData=[];
          that.authority=id.name;
          that.role_id=id.id;
         that.rolePrivileges();//
         that.role_referrer();//

        },

        //--
        rolePrivileges(){
          let that = this;
          that.$axios({
            url: ""+that.url_api+"/v1/common/rule_group/get_tree",
            method: "get",
            //headers: {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res.data.data);
                that.bind_list=res.data.data;
              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });
        },
    //
        role_referrer(){
          let that = this;
          console.log(that.role_id);
          that.$axios({
            url: ""+that.url_api+"/v1/common/role/get_tree",
            method: "post",
            data:{
              role_id:that.role_id,
            },
            //headers: {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res.data.data);
                that.toData=res.data.data;
              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });
        },

        //
        ensure(){
          console.log(this.role_id);
          let that=this;
          //
          //1id   that.role_id
          //2    that.role_checked
         that.toData.forEach(item =>{
            console.log(item.id)
          });
            that.$axios({
              url: ""+that.url_api+"/v1/common/role/bind_rule",
              method: "post",
              data:{
                role_id:that.role_id,//id
                rules:JSON.stringify(that.toData),//api
              },
              //headers: {"authorization": that.authorization_token,}
            })
              .then(res => {
                if (res.data.message=="success"){
                  console.log(res.data);
                }
                else{
                  console.log(res.data.message);
                }
              })
              .catch(error => {
                console.log(error);
              });



        },

















        //API
        compile(id,index){
          console.log(id);
          let that= this;
          that.checkedNames=[];
         that.authority=id.name;
         that.permission_id=id.id;
         // console.log( that.permission_id)
         that.api_methodes();
         that. bind_api();

        },


        //api

        bind_api(){
          let that = this;
          that.$axios({
            url: ""+that.url_api+"/v1/common/api/info_list",
            method: "post",
            data:{
              rule_id:that.permission_id,
            },
            //headers: {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res.data);
                // API
                res.data.data.list.forEach(item =>{
                  that.checkedNames.push(item.id)
                });
              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });

        },


        //Api
        saveInfo_api(){
          console.log(this.checkedNames);
          let that=this;
            that.$axios({
              url: ""+that.url_api+"/v1/common/rule/bind_api",
              method: "post",
                data:{
                rule_id:that.permission_id,//id
                apis:JSON.stringify(that.checkedNames),//api
          },
              //headers: {"authorization": that.authorization_token,}
            })
              .then(res => {
                if (res.data.message=="success"){
                  console.log(res.data);
                  that.checkedNames=[];
                }
                else{
                  console.log(res.data.message);
                }
              })
              .catch(error => {
                console.log(error);
              });

        },

        //apiapi
        searches(){
          let that=this;
          that.$axios({
            url: ""+that.url_api+"/v1/common/api/info_list",
            method: "post",
            data:{
              name:that.site_api,
            },
            //headers: {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res.data.data);
                // var resp = res.data.data;
                // var new_lists = resp['list'];
                // var total_count= resp['total_count'];
                that.api_list=res.data.data.list;
                console.log(that.rulelists)
              }
              else{
                // console.log(resp.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });

        },

        //
        permissionPage(page){
          let that=this
          console.log(page) // }
          //
          that.$axios({
            "url": ""+that.url_api+"/v1/common/rule/info_list",
            "method": "get",
            params:{
              page_num:page,
              page_size:that.pagesize
            },
            // "headers": {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res);
                // var resp = res.data.data;
                // var new_lists = resp['list'];
                // var total_count= resp['total_count'];
                that.rulelists=res.data.data.list
              }
              else{
                // console.log(resp.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });

        },


    //
        cancel(){
          let that=this;
          that.permission="";
          that. power();
        },

        //
        abolish(){
          this.masking=false
          this.name='',
            this.desc_en='',
            this.code='',
            this.root_id='',
            this.has_entry='',
            this.url='',
            this.creator='',
            this.create_time='',
            this.last_time='',
            this.parent_id=''

        },
      },

    }
</script>

