<template>
  <div class="mask" v-show="maskShow">
    <div class="mask-suspen">
      <div class="mask-head">
        <label></label>
      </div>
      <div class="mask-body">
        <div class="mask-left">
          <el-table
            :data="tableData"
            border
            stripe
            :header-cell-style="{background:'#68819E',color:'#ffffff'}"
            height="500px"
            size="mini"
            style="width: 100%">
            <el-table-column
              prop="img">
              <!--label="">-->
              <template slot-scope="scope">
                <img style="width: 20px;height: 20px"
                     :src="scope.row.img"/><!---->
                <!--label  -->
                <!--<span style="margin-left: 10px"></span>-->
              </template>
            </el-table-column>
            <el-table-column
              prop="name"
              label=""
              width="280">
            </el-table-column>
            <el-table-column
              prop="count"
              label="">
            </el-table-column>
          </el-table>
        </div>
        <div class="mask-right">
            <div class="mask-right-first">
              <label></label>
            </div>
            <div class="mask-right-second">
              <div>
                <input value="" style="width: 80px;border: none;margin-right: 10px;background: #ffffff" disabled readonly/>
                <el-date-picker
                  v-model="reservearrive"
                  type="datetime"
                  prefix-icon="el-icon-date"
                  placeholder=""
                  size="mini" style="width: 175px">
                </el-date-picker>
              </div>
              <div>
                <input value="" style="width: 80px;border: none;background: #ffffff" disabled readonly/>
                <input/>
              </div>
              <div>
                <input value="" style="width: 80px;border: none;background: #ffffff" disabled readonly/>
                <input/>
              </div>
            </div>
            <div class="mask-right-three">
              <label></label>
            </div>
            <div class="mask-right-fifth">
              <el-radio-group v-model="radioshift">
                <el-radio :label="3"></el-radio>
                <el-radio :label="6"></el-radio>
                <el-radio :label="9"></el-radio>
              </el-radio-group>
            </div>
            <div class="mask-right-last">
                <textarea></textarea>
            </div>
        </div>
      </div>
      <div class="mask-bottom">
        <button></button>
        <button @click="setMaskShow" style="background: #68819E"></button>
      </div>
    </div>
  </div>
</template>

<script>
  //
  import imgF from "@/assets/images/pms/shifting/F.png"
  export default {
    data() {
      return {
        //
        reservearrive:'',
        //
        radioshift:3,
        //
        maskShow:'true',
        //
        tableData: [{
          img: [require("@/assets/images/pms/shifting/F.png")],
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: ''
        }, {
          img: '',
          name: 'AR',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: 'AR',
          count: '1'
        }, {
          img: '',
          name: '',
          count: ''
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: ''
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }, {
          img: '',
          name: '',
          count: '1'
        }]
      }
    },

    methods: {
      //
      setMaskShow(){
        this.maskShow = !this.maskShow;
      },
    }
  }
</script>
<style lang="less">
  .el-table .cell {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    white-space: normal;
    word-break: break-all;
    height: 23px;
    //
    display: flex;
    align-items: center;
  }
  .mask{
    width: 100%;
    height: 100%;
    position: fixed;
    //opacity
    background: rgba(7, 13, 32, 0.2);
    top: 0;
    left: 0;
    z-index: 998;
    //
    .mask-suspen {
      background: #F5F5F5;
      width: 80%;
      height: 80%;
      position: fixed;
      z-index: 999;
      top: 10%;
      left: 10%;
      //
      .mask-head{
        height: 40px;
        background: #303A41;
        line-height: 40px;
        label{
          color: #ffffff;
          margin-left: 10px;
        }
      }
      //table
      .mask-body{
        border-bottom: 1px solid #CCCCCC;
        //table
        .mask-left{
          float: left;
          margin-top: 10px;
          margin-left: 10px;
          margin-bottom: 10px;
          width: 65%;
        }
        //
        .mask-right{
          height: 500px;
          float: left;
          margin-left: 10px;
          margin-top: 10px;
          width: 33%;
          .mask-right-first{
              label{
                margin-left: 20px;
              }
          }
          .mask-right-second{
              border: 1px solid #CCCCCC;
              background: #ffffff;
              margin-top: 10px;
              input{
                   margin-left: 10px;
                   height: 27px;
                   width: 175px;
                   margin-top: 10px;
                   margin-bottom: 10px;
                   border: 1px solid #CCCCCC;
                   border-radius: 4px;
              }
          }
          .mask-right-three{
            margin-top: 10px;
              label{
              margin-left: 20px;
              }
          }
          .mask-right-fifth{
            height: 100px;
            line-height: 100px;
            margin-top: 10px;
            padding-left: 20px;
            background: #ffffff;
            border: 1px solid #CCCCCC;
          }
          .mask-right-last{
            margin-top: 10px;
            height: 70px;
            textarea{
              width: 100%;
              height: 100%;
              border: 1px solid #CCCCCC;
            }
          }
        }

      }
      //
      .mask-bottom{
        button{
          float: right;
          margin-right: 10px;
          margin-top: 10px;
          height: 27px;
          width: 100px;
          border: none;
          color: #ffffff;
          line-height: 27px;
          background: #4488E9;
          border-radius: 4px;
        }
      }

    }
  }
</style>



