<template>
    <!---->
  <div class="ironsteward">
    <el-container>
      <!---->
      <el-aside  class="left"  style="width: 200px" >
        <div class="t-info">
          
        </div>
        <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
      </el-aside>
      <!---->
      <el-main  class="right" >
        <el-card class="box-card">
          <el-table
            :data="tableData"
            size="mini"
            style="width: 100%"
            :header-cell-style="{background:'#68819E',color:'#FFF'}">
            <el-table-column
              label="ID">
              <template slot-scope="scope">
                <span>{{ scope.row.equipmenttypeID }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="ID">
              <template slot-scope="scope">
                <span>{{ scope.row.ftypeID }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="">
              <template slot-scope="scope">
                <span>{{ scope.row.equichinadesc }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="">
              <template slot-scope="scope">
                <span>{{ scope.row.equienlishdesc }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="">
              <template slot-scope="scope">
                <span>{{ scope.row.ifbuildtable }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="RCU">
              <template slot-scope="scope">
                <span>{{ scope.row.ifRCU }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="">
              <template slot-scope="scope">
                <span>{{ scope.row.remarkinfo }}</span>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <a v-if="!scope.row.isEgdit" style="cursor: pointer"
                   size="mini"
                   @click="handleEdit(scope.$index, scope.row)"></a>
                <a v-if="scope.row.isEgdit" style="cursor: pointer"
                   size="mini"
                   @click="handleSuccessEdit(scope.$index, scope.row)"></a>
                <a
                  size="mini" style="cursor: pointer"
                  @click="handleDelete(scope.$index)"></a>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
    export default {
        name: "equipmenttype",
        data(){
          return {
            tableData: [{
              equipmenttypeID: '8301',
              ftypeID: 'RCU',
              equichinadesc: '',
              equienlishdesc : 'rcu',
              ifbuildtable : 'rcu',
              ifRCU :'rcu',
              remarkinfo : 'mr wang',
            }], data: [{
              label: '',
              children: [{
                label: '',
              },{
                label: '',
                },{
                label: 'RCU'
              }]
            }, {
              label: '',
              children: [{
                label: '',
              }, {
                label: '',
              }, {
                label: '',
              }, {
                label: ''
              }]
            }],
            defaultProps: {
              children: 'children',
              label: 'label'
            }
        };
      },
      methods:{
        //
        handleEdit(index, row) {
          this.$set(row,'isEgdit',true)
        },
        //
        handleDelete(index) {
          this.tableData.splice(index,1)
        },
        //
        handleSuccessEdit(index, row) {
          this.$set(row,'isEgdit',false)
        },
        //
        handleNodeClick(data) {
          console.log(data);
        }
      }
    }
</script>

<style lang="less" scoped>
  //
.left{
  .t-info{
    margin-top: 10px;
    margin-left: 1%;
    height: 30px;
    width: 100%;
    background: #EEEEEE;
  }
}
</style>



