<template>
  <div class="log">
    <div class="block">
      <span class="demonstration"></span>
      <el-date-picker v-model="value1"
                      type="datetime"
                      placeholder=""
                      prefix-icon="el-icon-date"
      ></el-date-picker>
      <span class="demonstration" style="margin-left: 32px"></span>
      <el-date-picker v-model="value1"
                      type="datetime"
                      placeholder=""
                      prefix-icon="el-icon-date"
      ></el-date-picker>
    </div>

    <div class="title">
      <div class="titleSearch">
        <span style="margin-left: 36px"> :</span>
        <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="state">
        <span style="margin-right: 64px"> :</span>
        <el-checkbox v-model="checked" style="display: inline-block"></el-checkbox>
        <el-checkbox v-model="checked" style="display: inline-block"></el-checkbox>
      </div>
      <!---->
      <el-table
        :data="itemList"
        :header-cell-style="{background:'#68819EFF',color:'white'}"
        stripe
        style="width: 94%;margin-left: 24px">
        <el-table-column
          prop="name"
          label="">
        </el-table-column>
        <el-table-column
          prop="desc_en"
          label="">
        </el-table-column>
        <el-table-column
          prop="code"
          label="">
        </el-table-column>
        <el-table-column
          prop="root_id"
          label="">
        </el-table-column>
        <el-table-column
          prop="has_entry"
          label="">
        </el-table-column>
        <el-table-column
          prop="url"
          label="">
        </el-table-column>
        <el-table-column
          prop="creator"
          label="">
        </el-table-column>
        <el-table-column
          prop="create_time"
          label="">
        </el-table-column>
        <el-table-column
          prop=" versions"
          label="">
        </el-table-column>
        <el-table-column
          prop="last_time"
          label="">
        </el-table-column>
        <el-table-column
          prop="address"
          label="">
          <template slot-scope="scope">
            <el-button @click="cancel(scope.row.id,scope.$index, newlists)"type="text" size="small"></el-button>
            <el-button type="text" size="small" @click="submit(scope.row,scope.row.id,scope.$index, newlists)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination style="float: right;margin-right: 4%"
                     background
                     layout="prev, pager, next"
                     :page-size="pagesize"
                     @current-change="handleCurrentChange"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
    export default {
        name: "log",
      data() {
        return {
          checked:false,//
          pickerOptions1: {
            shortcuts: [{
              text: '',
              onClick(picker) {
                picker.$emit('pick', new Date());
              }
            }, {
              text: '',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24);
                picker.$emit('pick', date);
              }
            }, {
              text: '',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', date);
              }
            }]
          },
          value1: '',
          value2: '',
          value3: '',
          itemList: [
            {
              projectName: '',

            },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // },
            // {
            //   projectName: '',
            //
            // }

          ],
          amend:true,
          save:false,
          options: [{
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          }, {
            value: '4',
            label: ''
          }, {
            value: '5',
            label: ''
          }],
          value: '',
          pagesize:6, //
          total:16,//

        };
      },
      methods:{
        //
           alter(){
          this.amend=false,
            this.save=true
        },
       //
           cancel(){
          this.amend=true,
            this.save=false
        },
        //
           handleCurrentChange(curPage) {
          console.log(curPage) // }
          this.curPage=curPage;

        },
      }
    }
</script>

<style   lang="less" scoped>
  .log{
    width: 100%;
    .block{
      margin-top:50px;
      margin-left: 96px;
      .demonstration{
        font-size: 18px;
      }
    }
    .title{
      margin-top: 28px;
      font-size: 16px;
      margin-left: 60px;

      .titleSearch{
        position: relative;
        display: inline-block;
        .btn{
          position: absolute;
          top: 0;
          right: 0;
          width: 30px;
          height: 30px;
          background: #EEEEEEFF;
          display: inline-block;
        }
        .search{
          width: 300px;
          height: 30px;
          padding-left: 15px;
          margin-left: 60px;
        }
      }


      .state{
        display: inline-block;
        margin-left: 86px;
        margin-bottom: 40px;
      }

      .amend ,.save{
        /*width: calc(100% - 128px);*/
        height: 44px;
        line-height: 44px;
        background:rgba(247,247,247,1);
        margin-bottom: 7px;
        overflow: hidden;
        min-width: 926px;
        position: relative;
        div{
          position: absolute;
          top: 0px;
          right: 0;
          span{
            font-size:14px;
            font-family:PingFangSC-Regular;
            font-weight:400;
            color:rgba(104,129,158,1);
          }
        }
      }
    }
  }
</style>
<style>
  .el-input__prefix{
    font-size: 20px;
    color: black;
  }
  /*.el-date-editor.el-input{*/
    /*width: 300px;*/
  /*}*/
  .el-input__inner{
    padding-left: 50px;
  }
</style>



