<template>
  <div class="information">
    <p></p>
     <!--<div class="state">-->
       <!--<span style=" margin-right: 48px;margin-left:100% "></span>-->
       <!--<el-checkbox v-model="checked" style="display: inline-block"></el-checkbox>-->
       <!--<el-checkbox v-model="checked" style="display: inline-block"></el-checkbox>-->
     <!--</div>-->
    <!---->
    <el-table
      :data="newlists"
      fit
      :header-cell-style="{background:'#68819EFF',color:'white',}"
      style="width: 96%;margin-left: 24px;">
      <el-table-column
        prop="name"
        fixed
        label="">
      </el-table-column>
      <el-table-column
        prop="desc_en"
        label="">
      </el-table-column>
      <el-table-column
        prop="code"
        label="">
      </el-table-column>
      <el-table-column
        prop="root_id.name"
        label="">
      </el-table-column>
      <el-table-column
        prop="has_entry"
        width="90"
        label="">
        <!---->
        <template slot-scope="scope">
          <span v-if="scope.row.has_entry === 0"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column
        prop="url"
        label="">
      </el-table-column>
      <el-table-column
        prop="creator.real_name"
        label="">
      </el-table-column>
      <el-table-column
        prop="create_time"
        width="150"
        label="">
      </el-table-column>
      <el-table-column
        prop="version"
        width="90"
        label="">
      </el-table-column>
      <el-table-column
        prop="last_time"
        width="150"
        label="">
      </el-table-column>
      <el-table-column
        prop="address"
        width="190"
        fixed="right"
        label="">
        <template slot-scope="scope">
          <el-button @click="cancel(scope.row.id,scope.$index, newlists)"type="danger" size="mini" ></el-button>
          <el-button size="small" @click="submit(scope.row,scope.row.id,scope.$index, newlists)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination style="float: right;margin-right: 4%"
      layout="prev, pager, next"
      :page-size="pagesize"
      @current-change="handleCurrentChange"
      :total="total">
    </el-pagination>
    <!---->
    <div class="masking" v-show="masking">
      <ul class="mask_information">
        <li><span ></span><el-input v-model="name" placeholder="" style="width: 217px;"></el-input></li>
        <li><span ></span><el-input v-model="desc_en" placeholder="" style="width: 217px;"></el-input></li>
        <li><span ></span><el-input v-model="code" placeholder="" style="width: 217px;"></el-input></li>
        <li>
          <span ></span>
          <el-input v-model="input_kind"  style="width: 217px;" :disabled="true" ></el-input>
        </li>
        <li>
          <span ></span>
          <el-select v-model="has_entry" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </li>
        <li><span ></span><el-input v-model="url" placeholder="" style="width: 217px;"></el-input></li>
        <!--<li><span ></span><el-input v-model="creator" placeholder="" style="width: 300px;"></el-input></li>-->
        <!--<li><span ></span><el-input v-model="create_time" placeholder="" style="width: 300px;"></el-input></li>-->
        <li>
          <span ></span>
          <el-input v-model="input_father"  style="width: 217px;" :disabled="true" ></el-input>
        </li>
        <li><span ></span><el-input v-model="input" placeholder="1.0" style="width: 217px;" :disabled="true" ></el-input></li>
        <!--<li><span ></span><el-input v-model="last_time" placeholder="" style="width: 300px;"></el-input></li>-->

        <li style="margin-top:50px;"><button @click="preserve"></button> <button @click="abolish"></button></li>



      </ul>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
    export default {
        name: "module-information",
      data(){
          return{
            api:'http://47.98.113.173:9519',
            labelModel:'',
            masking:false,
            checked:false,//
            // 
            name:'',//
            desc_en:'',//
            code:'',//
            root_id:'',//
            has_entry:'',//
            url:'',//
            creator:'',//
            create_time:'',//
            last_time:'',//
            parent_id:'',//
            id:'',//id 
            pagesize:10, //
            // total:10,//
            input_kind:'',//
            input_father:'',//

            options: [{
              value: '0',
              name: ''
            }, {
              value: '1',
              name: ''
            },],
            value: '',
            input:'',
            newlists_info:[]
          }
      },
      //
      props:{
        newlists:{type:Array,required:true},
        authorization_token:{type:String,required:true},
        total:{type:Number,required:true},
      },

      created:function(){

      },
      methods:{
        //
        abolish(){
            this.masking=false
            this.name='',
            this.desc_en='',
            this.code='',
            this.root_id='',
            this.has_entry='',
            this.url='',
            this.creator='',
            this.create_time='',
            this.last_time='',
            this.parent_id=''

        },
        //
        preserve(){
          if (this.has_entry==""){
            this.has_entry=1
          }else if(this.has_entry==""){
            this.has_entry=0
          };
          console.log("");
          console.log(this.creator)
          this.$axios({
            url:""+this.api+"/v1/common/module/update/"+this.id,
            method: "post",
            // params:{},
            data:{
              name:this.name,
              desc_en:this.desc_en,
              code:this.code,
              root_id:this.root_id,
              has_entry:this.has_entry,
              url:this.url,
              creator:this.creator.name,
              // create_time:this.create_time,
              // last_time:this.last_time,
              parent_id:this.parent_id
            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                location.reload();  //
                // this.$axios.get('"+that.api+"/v1/common/module/info_list',
                //   {
                //     //headers: {
                //       'authorization': this.authorization_token,
                //     }})
                //   .then(res=>{
                //     console.log(res.data);
                //     if(res.data.message==="success"){
                //       this.masking=false
                //       this.newlists=res.data.data.list
                //       // console.log(this.newlists_info);
                //
                //
                //     }
                //
                //   })
                //   .catch(error=>{
                //     console.log(error);
                //   })
              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })
        },
        //
        submit: function (srow,id,index,list) {
          this.masking=true
          this.id=id;
          console.log(id);
          console.log(index);
          console.log(list);
          console.log(srow);
          this.name=srow.name;
          this.desc_en=srow.desc_en;
          this.code=srow.code;
          // this.root_id=srow.root_id;
          this.has_entry=srow.has_entry;
          this.url=srow.url;
          this.creator=srow.creator;
          this.versions=srow.versions;
           this.input_kind=srow.root_id.name;//
            // this.input_father=srow.parent_id;//
          if(!srow.parent_id){
            this.input_father=srow.parent_id;
          }else {
            this.input_father=srow.parent_id.name;
          };
          if(srow.has_entry==1){
            this.has_entry=""
          }else  if(srow.has_entry==0){
            this.has_entry=""
          }


        },
        //
        handleCurrentChange(curPage) {
          let that=this
          console.log(curPage) // }
          //
          that.$axios({
            url: ""+that.api+"/v1/common/module/info_list",
            method: "get",
            params:{
              page_num:curPage,
              page_size:that.pagesize
            },
            //"headers": {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res);
                // var resp = res.data.data;
                // var new_lists = resp['list'];
                // var total_count= resp['total_count'];
                that.newlists=res.data.data.list
              }
              else{
                // console.log(resp.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });


        },
        //
          cancel(id,index,list){
          console.log(id);
          console.log(index);
          console.log(list);
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
            list.splice(index, 1);
            this.$axios({
              url:""+this.api+"/v1/common/module/remove/"+id,
              method: "post",
              // params:{},
              // data:{},
              //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{
                console.log(res.message)
                if(res.data.message==="success")
                location.reload(); // 
              })
              .catch(error=>{
                console.log(error);
              })



          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });


        },

      }

    }

</script>

<style lang="less" scoped>
 .information{
   width:100% ;
   height: 100%;
   /*min-width: 860px;*/
   /*background:rgba(238,238,238,1);*/
   p{
    display: inline-block;
     width:142px;
     height:35px;
     font-size:26px;
     font-family:Adobe Heiti Std R;
     font-weight:normal;
     color:rgba(51,51,51,1);
     line-height:24px;
     /*margin: 42px  0px 38px 22px;*/
     margin-top: 20px;
     margin-left: 22px;
   }
   .state{
     width: calc(100% - 470px);
     display: inline-block;
     font-size:18px;
     margin-top: 42px;

   }
   .title{
     width: calc(100% - 50px);
     height: 40px;
     background: #68819EFF;
     /*margin: 0 104px  0  24px;*/
     margin-left: 24px;
     line-height: 40px;
     display: flex;
     min-width: 1200px;
     li{
       width: 102px;
       font-size:14px;
       font-family:Adobe Heiti Std R;
       font-weight:normal;
       color:rgba(255,255,255,1);
       text-align: center;
     }
   }
   .amend ,.save{
     width: calc(100% - 50px);
     min-width: 1200px;
     height: 44px;
     margin-left: 24px;
     line-height: 44px;
     background:rgba(247,247,247,1);
     margin-bottom: 7px;
     position: relative;
     display: flex;
     input{
       width:101.7px;
       background:rgba(247,247,247,1);
       border: none;
       outline:none;
       text-align: center;
     }
     div{
       position: absolute;
       top: 0px;
       right: 0;
       span{
         font-size:14px;
         font-family:PingFangSC-Regular;
         font-weight:400;
         color:rgba(104,129,158,1);
       }
     }
   }
   /**/
   .masking{
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     background: rgba(7, 13, 32, 0.3);
     z-index: 1000;
     .mask_information{
       width: 70%;
       height: 70%;
       background: white;
       margin: 7% auto;
       li{
         width: 45%;
         display: inline-block;
         margin-left: 30px;
         margin-top: 20px;
         float: left;
         span{
           width: 100px;
           display: inline-block;
           text-align: left;

         }
         button{
           width: 80px;
           height: 40px;
           line-height: 40px;
           text-align: center;
           background:rgba(68,136,233,1);
           color: white;
           border: none;
           border-radius:4px ;
           outline: none;
           margin-left: 20px;
         }
       }

     }
   }
 }
</style>
<style >
  .el-table .cell{
    text-align:center;
  }
</style>



