<template>
  <div class="permissions">
    <div class="tree" >
      <div class="moduleTree"><span style="color: black"></span></div>
      <div class="content">
        <div class="control">
          <el-tree
            :data="role_set"
            node-key="node.id"
            :expand-on-click-node="false"
            node-contextmenu
            @node-click="handleNodeClick">
                 <span class="custom-tree-node" slot-scope="{ node, data }">
                     <span class="my_node" :data-id="data.id">{{data.name}}</span>
                    <span style="position: absolute;right: 20px">
                      <!-- v-show="setpro"-->
                      <i class="el-icon-edit" style="margin-right: 30px " @click="() => revise(node, data)" ></i>
                      <i class="el-icon-delete" @click="() => cancel  (node, data)" style="margin-right: 30px "></i>
                      <!--<i class="el-icon-circle-plus" @click="() => append(data)" style="margin-right: 30px "></i>-->
                    </span>
                 </span>
          </el-tree>
          <p @click="add">+</p>
        </div>
        </div>
      <div class="moduleTree"></div>
      <div class="content">
        <div class="control">
          <el-tree
            :data="permission_group"
            node-key="node.id"
            :expand-on-click-node="false"
            node-contextmenu
            @node-click="nodeClick">
                 <span class="custom-tree-node" slot-scope="{ node, data }">
                     <span class="my_node" :data-id="data.id">{{data.name}}</span>
                    <span style="position: absolute;right: 20px">
                      <i class="el-icon-edit" style="margin-right: 30px " @click="() => amend(node, data)" ></i>
                      <i class="el-icon-delete" @click="() => remove(node, data)" style="margin-right: 30px "></i>
                      <!--<i class="el-icon-circle-plus" @click="() => append(data)" style="margin-right: 30px "></i>-->
                    </span>
                 </span>
          </el-tree>
          <p @click="append">+</p>
        </div>
      </div>
    </div>

    <div class="router">
      <child ref="mychild"  :role_set="role_set"  :permission_group="permission_group"></child>
      <div class="ini"><logon-view></logon-view></div>
    </div>
    <!---->
    <div class="masking" v-show="masking">
      <!---->
      <ul class="mask_information" v-show="role">
        <!--<div class="hint" v-show="masking_hint"></div>-->
        <li>
          <span ></span><el-input v-model="name" placeholder="" style="width: 217px;"   ></el-input>

        </li>
        <li><span ></span><el-input v-model="code" placeholder=""  style="width: 217px;" ></el-input></li>
        <li>
          <span ></span><el-select v-model="status" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li><span ></span><el-input v-model="desc"   style="width: 217px;"  placeholder="" ></el-input></li>
        <li><span ></span><el-input v-model="desc_en" placeholder=""   style="width: 217px;" ></el-input></li>
        <li  v-show="hotel_pub"><span ></span><el-select v-model="hotel_id" placeholder="">
          <el-option
            v-for="item in part_hotel_list"
            :key="item.value"
            :label="item.full_name"
            :value="item.id">
          </el-option>
        </el-select>
          </li>
        <li v-show="pub_hotel"><span ></span><el-input v-model="pub"   style="width: 217px;"  :disabled="true"   placeholder=""></el-input></li>
        <li   v-show="pub_hotel" style="margin-top:50px;"><button @click="preserve"></button> <button @click="abolish"></button></li>
        <li v-show="hotel_pub" style="margin-top:50px;"><button @click="conserve"></button> <button @click="abolish"></button></li>
      </ul>
      <!---->
      <ul class="mask_information" v-show="right_control">
        <!--<div class="hint" v-show="masking_hint"></div>-->
        <li><span ></span><el-input v-model="permission_name" placeholder="" style="width: 217px;"  ></el-input></li>
        <li><span ></span><el-input v-model="permission_code" placeholder="" style="width: 217px;"></el-input></li>
        <li>
          <span ></span><el-select v-model="status_bar" placeholder=""><el-option
            v-for="item in options"
            :key="item.value"
            :label="item.name"
            :value="item.value">
          </el-option>
        </el-select>
        </li>
        <li><span ></span><el-input v-model="chinese_name" placeholder=""  style="width: 217px;"></el-input></li>
        <li><span ></span><el-input v-model="english_name"  style="width: 217px;"   placeholder=""></el-input></li>
        <li v-show="hotel_pub"><span ></span><el-select v-model="permission_hotel" placeholder=""><el-option
          v-for="item in part_hotel_list"
          :key="item.value"
          :label="item.full_name"
          :value="item.id">
        </el-option>
        </el-select></li>
        <li v-show="pub_hotel"><span ></span><el-input v-model="pub"  style="width: 217px;" :disabled="true"   placeholder=""></el-input></li>
        <li v-show="hotel_pub" style="margin-top:50px;"><button @click="keep"></button> <button @click="abolish"></button></li>
        <li v-show="pub_hotel" style="margin-top:50px;"><button @click="keep_pub"></button> <button @click="abolish"></button></li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Child from './role-privileges';
  let id = 1000;
    export default {
        name: "permissions",
      components: {
        child: Child
      },
      data() {
        const data = [{

        }];


        return {

          options: [{
            value: '0',
            name: ''
          }, {
            value: '1',
            name: ''
          }],
          value: '',
          url_api:'http://47.98.113.173:9519',
          activeName2: 'first',
          data4: JSON.parse(JSON.stringify(data)),
          data5: JSON.parse(JSON.stringify(data)),
          index:this.data,
          setpro:false,//
          setpro_group:false,//
          authorization_token:'',//token
          permission_group:[],//
          masking:false,//
          right_control:false,//
          role:false,//
          permission_hotel:'',//
          // 
          name:'',////
          role_group:'',//
          roles_id:'',//id
          code:'',//
          status:'',//
          desc:'',//
          desc_en:'',//
          hotel_id:'',//
          grogshop_id:'',//id
          right_control:false,//
          role:false,//
          role_set:[],//
          root_id:'',//
          has_entry:'',//
          url:'',//
          authorization_token:'', //token
          newlists:[],//
          setpro:false,//
          save_add:false,//
          save_childen:false,//
          save_preserve:false,//
          //  
          permission_name:'',//
          api:'',//API
          status_bar:'',//
          chinese_name:'',//
          english_name:'',//
          associated:'',//
          permission_name_id:'',//id
          role_set_id:'',//id
          permission_code:'',//
          pub:'',//  
          pub_hotel:false,// 
          hotel_pub:false,//   
          part_hotel_list:[],//

        };
      },
      created:function () {
        let that=this;
        //
        that.part();
        //  
        that.permission();
        //
        that.part_hotel();

      },
      methods: {
          //
        permission(){
          let that=this
          that.$axios({
            url: ""+that.url_api+"/v1/common/rule_group/info_list",
            method: "post",
            data:{
              page_size:9999,
            },

           //headers: {"authorization": that.authorization_token}
          })
            .then(res=>{
              if (res.data.message=="success"){
                console.log(res.data.data)
                that.permission_group=res.data.data.list
              }
              else{
                console.log(res.data.message);
              }

              // that.newlists=res.data.data
            })
            .catch(error=>{
              console.log(error);
            });
        },

          //
        part(){
          let that=this;
          that.$axios({
            url: ""+that.url_api+"/v1/common/role_group/info_list",
            method: "get",
           //headers: {"authorization": that.authorization_token}
          })
            .then(res=>{
              if (res.data.message=="success"){
                console.log(res.data.data)
                that.role_set=res.data.data.list;
                console.log(that.permission_group);
              }
              else{
                console.log(res.data.message);
              }

              // that.newlists=res.data.data
            })
            .catch(error=>{
              console.log(error);
            });

        },

        //
        part_hotel(){
          let that=this;
          that.$axios({
            url: ""+that.url_api+"/v1/common/hotel/info_list",
            method: "get",
           //headers: {"authorization": that.authorization_token}
          })
            .then(res=>{
              if (res.data.message=="success"){
                console.log(res.data.data.list)
                that.part_hotel_list=res.data.data.list;
              }
              else{
                console.log(res.data.message);
              }

              // that.newlists=res.data.data
            })
            .catch(error=>{
              console.log(error);
            });

        },

        // 
        check_repeat(field, keyword){
          // console.log(this.urls.indexOf(keyword));
          // field 
          //keyworld 
          // 
          if(field === "name"){
            if(this.names.indexOf(keyword)==-1){
              // alert("bu")
            }else {
              alert("")
            }
          }
          else if(field === "name"){
            if(this.permission_name.indexOf(keyword)==-1){
              // alert("bu")
            }else {
              alert("")
            }
          }
          else{
            //
          }
        },


        // 
        unfocused(field, keyword){
          this.check_repeat(field, keyword);
        },

        // 
        handleNodeClick( index){
          this.setpro=true;
        },
        // 
        nodeClick(index){
          this.setpro_group=true;
        },
       //
        add(){
          console.log(1);
          this.masking=true;
          this.role=true;
          this.right_control=false;
          this.pub_hotel=false;
          this.hotel_pub=true
        },
        //
        conserve(){
          if (this.status_bar==""){
            this.status_bar=1
          }else if(this.status_bar==""){
            this.status_bar=0
          };

          console.log("");
          console.log(this.creator)
          this.$axios({
            url:""+this.url_api+"/v1/common/role_group/add",
            method: "post",
            // params:{},
            data:{
              name:this.name,
              desc:this.desc,
              desc_en:this.desc_en,
              code:this.code,
              status: this.status_bar,
              hotel_id:this.hotel_id
              // url:this.api,

            },
           //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                this.masking=false
                console.log(this.name);
                this. part();

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })

        },
        //
        revise(node,data){
          this.masking=true;
          this.right_control=false;
          this.role=true;
          this.pub_hotel=true;
          this.hotel_pub=false;
          console.log(node, data);
          this.name=data.name;
           this.code=data.code;
           this.desc=data.desc;
            this.desc_en=data.desc_en;
           this.pub=data.hotel_id.short_name;
           this.role_set_id=data.id
          if (data.status==1){
            this.status=""
          }else if(data.status==0){
            this.status=""
          };

        },

        //
        preserve(){
          console.log('');
          if (this.status==""){
            this.status=1
          }else if(this.status==""){
            this.status=0
          };

          console.log("");
          this.$axios({
            url:""+this.url_api+"/v1/common/role_group/update/"+this.role_set_id,
            method: "post",
            // params:{},
            data:{
              name:this.name,
              code:this.code,
              desc:this.desc,
              desc_en:this.desc_en,
              status:this.status,
              // hotel_id:this.grogshop_id,
            },
           //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{

              if(res.data.message==="success"){
                //location.reload();  //
                console.log(this.name);
                this.part();
                this.masking=false;

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })




        },


        //
        keep(){
          if (this.status_bar==""){
            this.status_bar=1
          }else if(this.status_bar==""){
            this.status_bar=0
          };

          console.log("");
          console.log(this.creator)
          this.$axios({
            url:""+this.url_api+"/v1/common/rule_group/add",
            method: "post",
            // params:{},
            data:{
              name:this.permission_name,
              desc:this.chinese_name,
              desc_en:this.english_name,
              code:this.permission_code,
              status: this.status_bar,
              hotel_id:this.permission_hotel
              // url:this.api,

            },
           //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                 this.masking=false
                console.log(this.permission_name);
                this.permission();

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })

        },


        //
        abolish(){
          this.masking=false
          this.name='',
            this.desc_en='',
            this.code='',
            this.root_id='',
            this.has_entry='',
            this.url='',
            this.creator='',
            this.create_time='',
            this.last_time='',
            this.parent_id=''

        },

        //authorization
        authorization() {
          let that=this;
          this.$axios.get(""+that.url_api+"/v1/common/employee/create_token?user_id=1&user_name=super_root",
          )
            .then(res=>{
              if (res.data.message=="success"){
                console.log(res.data.authorization);
                that.authorization_token= that.authorization_token === ""? res.data.authorization: that.authorization_token;
                console.log(that.authorization_token);
                that.new_authorization(res.data.message)
                // //
                // that.part();
                // //  
                // that.permission();
                // //
                // that.part_hotel();
              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })
        },
        //
        new_authorization(msg){
          let that=this;
          if(msg==="authorization invalid"){
            that.authorization()
          }
        },


        handleClick(tab, event) {
          // console.log(tab, event);
        },
        //
        append() {
          this.masking=true
          this.right_control=true;
          this.role=false;
          this.pub_hotel=false;// 
          this.hotel_pub=true;//   
        },

        //
        amend(node, data){
          console.log(node,data)
          this.masking=true;
          this.right_control=true;
          this.role=false;
           this.pub_hotel=true,// 
           this.hotel_pub=false,//   
          this.permission_name=node.data.name;//
          this.permission_code=node.data.code;//API
          this.status_bar=node.data.status;//
          if(node.data.status===0){
            this.status_bar=""
          }else  if(node.data.status===1){
            this.status_bar=""
          }
          this.permission_name_id=node.data.id;//id
          this.chinese_name=node.data.desc;//
          this.english_name=node.data.desc_en;//
          this.pub= node.data.hotel_id
        },

        //
        keep_pub(){
          if (this.status_bar==""){
            this.status_bar=1
          }else if(this.status_bar==""){
            this.status_bar=0
          };

          console.log("");
          this.$axios({
            url:""+this.url_api+"/v1/common/rule_group/update/"+this.permission_name_id,
            method: "post",
            // params:{},
            data:{
              name:this.permission_name,
              desc:this.chinese_name,
              desc_en:this.english_name,
              status: this.status_bar,
              code:this.permission_code,
              url:this.api,

            },
           //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              console.log(res);
              if(res.data.message==="success"){
                // location.reload();  //
                this.permission();
               this.masking=false;

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })
        },

        //--
        cancel (node, data){
          const parent = node.parent;
          const children = parent.data || parent.data;
          const index = children.findIndex(d => d.id === data.id);
          console.log(node);
          let id = node.data.id
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
            children.splice(index, 1);
            this.$axios({
              url:""+this.url_api+"/v1/common/role_group/remove/${id}",
              method: "post",
              // params:{},
              // data:{},
             //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{

                if(res.data.message==="success"){
                  console.log(res)
                  // location.reload(); // 
                }

              })
              .catch(error=>{
                console.log(error);
              })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '',

            });
          });

        },

        //---
        remove(node, data) {
          const parent = node.parent;
          const children = parent.data || parent.data;
          const index = children.findIndex(d => d.id === data.id);
          console.log(node);
          let id = node.data.id
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
            children.splice(index, 1);
            this.$axios({
              url:""+this.url_api+"/v1/common/rule_group/remove/"+id,
              method: "post",
              // params:{},
              // data:{},
             //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{

                if(res.data.message==="success"){
                  console.log(res)
                  // location.reload(); // 
                  this.permission();
                }

              })
              .catch(error=>{
                console.log(error);
              })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '',

            });
          });


        },

      }
    }
</script>

<style  lang="less"  scoped>
  .permissions{
    width: 100%;
    height: 93%;
    overflow: hidden;
    /*overflow-y: scroll;*/
    //min-width: 1800px;
    position: relative;
    .tree{
      height: 100%;
      width: 300px;
      /*min-height: 1000px;*/
      /*background:rgba(247,247,247,1);*/
      float: left;
      .serialNumber{
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        padding-left: 20px;
        span{
          margin-left: 20px;
        }
      }
      .moduleTree{
        width:110%;
        height:40px;
        background:rgba(238,238,238,1);
        font-size:16px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:black;
        line-height:40px;
        margin-left: -15px;
        padding-left: 98px;
      }
      .content{
        width: 100%;
        height:46%;
        /*background:rgba(247,247,247,1);*/
        overflow-y: scroll;
        .control{
          width: 100%;

          p{
            font-size: 26px ;
            background:rgba(247,247,247,1);
            padding-left: 84px;

          }
        }
      }


    }


    .router{
      width:calc(100% - 310px);
      height: 100%;
      font-size:20px;
      float: left;
      overflow-y: scroll;
      //min-width: 300px;
      /*min-height: 1000px;*/
      white-space:nowrap;
      background:rgba(247,247,247,1);
      .ini{
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;

      }
    }
    /**/
    .masking{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(7, 13, 32, 0.3);
      z-index: 1000;
      .mask_information{
        width: 70%;
        height: 70%;
        background: white;
        margin: 7% auto;
        li{
          width: 45%;
          display: inline-block;
          margin-left: 30px;
          margin-top: 20px;
          float: left;
          span{
            width: 100px;
            display: inline-block;
            text-align: left;

          }
          button{
            width: 80px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            background:rgba(68,136,233,1);
            color: white;
            border: none;
            border-radius:4px ;
            outline: none;
            margin-left: 20px;
          }
        }

      }
    }

  }
</style>

<style >
  /*.expanded{*/
    /*display: none;*/
  /*}*/

  /*.qq/deep/ .el-checkbox{*/
    /*display: none;*/
  /*}*/

</style>



