            margin-top: 10px;
            label{
              .common-mask-lable;
            }
            input{
              margin-left: 10px;
              width: 80px;
              height: 27px;
              border: 1px solid #CCCCCC;
              border-radius: 4px;
            }
            button{
              margin-left: 10px;
              width: 70px;
              height: 25px;
              background: gray;
              border: none;
              line-height: 25px;
              color: #FFFFFF;
              font-size: small;
              border-radius: 25px;
            }

          }
      }
      //
      .mask-cardtype{
              margin-top: 10px;
              height: 260px;
              background: #ffffff;
              border-bottom: 1px solid #CCCCCC;
              //div
              div{
                height: 75px;
                margin-top: 10px;
                label{
                  .common-mask-lable;
                }
                input{
                  width: 200px;
                  margin-left: 10px;
                  height: 27px;
                  border: 1px solid #CCCCCC;
                  border-radius: 4px;
                }
              }
      }
      //div-btn
      .mask-btn{
        img{
          position: absolute;
          margin-left: 10px;
          margin-top: 80px;
          width: 90px;
          height: 30px;
        }
        button{
          position: relative;
          margin-top: 80px;
          margin-left: 10px;
          height: 30px;
          width: 80px;
          color: #ffffff;
          line-height: 30px;
          font-size: small;
          background: #8895A8;
          border: none;
        }
        .btn-last{
          float: right;margin-right: 10px;background: #58D56D;margin-top: 80px
        }
      }
    }
  }
  /**
  
  */
  .common-mask-lable{
    //lable
    margin-left: 10px;
    font-size: 16px;
    color: #333333;
  }
  .common-input{
    width: 8rem;
    height: 25px;
    border: 1px solid #CCCCCC;
    margin-left: 10px;
  }
  .common-marleft{
    margin-left: 10px;
  }
  .common-font{
    margin-top: 20px;
    margin-left: 10px;
    font-size: small;
    color: #777777;
  }
  .common-btn{
    margin-left: 10px;
    width: 80px;
    height: 25px;
    background: #4488E9;
    line-height: 25px;
    color: #FFFFFF;
    border: none;
    margin-right: 40px;
  }
  .select-sty{
    margin-top: 10px;
  }
  /**
   
   */
  .order-see{
    margin-top: 10px;
    color: #777777;
    img{
      .common-marleft
    }
    label{
      .common-marleft
    }
    .add-info{
       float: right;
       margin-right: 10px;
    }
  }
  /**
  
   */
  .tab-class{
    border: 1px solid #CCCCCC;
       .common-marleft;
       margin-top: 10px;
       margin-right: 10px;
    //
    .all-type{
      height: 30px;
      background: #F4F4F4;
      line-height: 30px;
      padding-left: 10px;
    }
    //
    .order-type{
      .common-font
    }
    //
    .room-type{
      .common-font
    }
    //
    .order-source{
      .common-font
    }
    //
    .order-customer{
      .common-font
    }
    .room-select-bottom{
      margin-top: 10px;
    }
    .room-type-bottom{
      margin-top: 20px;
      margin-left: 196px;
      margin-bottom: 10px;
    }
  }
  /**
    
     */
  .order-table{
    border: 1px solid #CCCCCC;
    margin-top: 20px;
    margin-right: 10px;
    .select-sty;
    .common-marleft;
  }
  /**
  
   */
  .order-page{
    .common-marleft;
    .select-sty;
    color: #777777;
    .pull-right {
      float: right;
      margin-right: 10px;
    }
  }
  /**
  
   */
  .order-screen{
    .common-marleft;
    .select-sty;
    .btn{
      margin-left: 10px;
      width:100px;
      height:27px;
      background:rgba(68,136,233,1);
      border-radius:4px;
      border: none;
      color: #ffffff;
    }
  }
</style>
<style>
  .el-input-number__decrease{
    right: 1px;
    border: 0px solid #dcdfe6;
  }
  .el-input-number__increase{
    right: 1px;
    border: 0px solid #dcdfe6;
  }
  .el-input-number__decrease, .el-input-number__increase{
    opacity: 1;
    background: #ffffff;
    height: 0;
    /*margin-top: 15px;*/
    /*line-height: 24px;*/
  }

  .el-input-number{
    margin-left: 10px;
    outline: none;
  }


</style>



