<template>
  <!---->
  <el-row class="container">
    <!---->
    <el-col :span="24" class="topbar-wrap">
      <div class="topbar-logo topbar-btn">
        <router-link to="/"><img src="@/assets/logo.png" style="padding-left:8px;"></router-link>
      </div>
      <div class="topbar-logos" >
        <a href="/" style="color: #fff;"><span v-show="!$store.state.collapsed"></span></a>
      </div>
      <div class="topbar-title">
        <el-row v-show="$store.state.topNavState==='home'"><!-- topNavStatetype -->
          <el-col :span="24">
            <el-menu :default-active="defaultActiveIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" :router="true">
              <el-menu-item index="/main"></el-menu-item>
              <el-menu-item index="/houseStatus"></el-menu-item>
              <el-menu-item index="/orderList"></el-menu-item>
              <el-menu-item index="/accountsReceivable"></el-menu-item>
              <el-menu-item index="/customer"></el-menu-item>
              <el-menu-item index="/marketing"></el-menu-item>
              <el-menu-item index="/invoicing"></el-menu-item>
              <el-menu-item index="/reports"></el-menu-item>
              <el-menu-item index="" @click="setMaskShow"></el-menu-item>
              <el-menu-item index="/nightaudit"></el-menu-item>
              <el-menu-item index="/setting/roomSet"></el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </div>
      <div class="topbar-account topbar-btn">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link userinfo-inner">
            <i class="iconfont icon-user"></i> {{nickname}}   <i class="el-icon-caret-bottom"></i></span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <div @click="jumpTo('/user/profile')"><span style="color: #555;font-size: 14px;"></span></div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="jumpTo('/user/changepwd')"><span style="color: #555;font-size: 14px;"></span></div>
            </el-dropdown-item>
            <el-dropdown-item divided @click.native="logout"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!---->
        <div class="mask" v-show="maskShow">
          <div class="mask-suspen">
            <div class="mask-head">
              <label></label>
            </div>
            <div class="mask-body">
              <div class="mask-left">
                <el-table
                  :data="tableData"
                  border
                  stripe
                  :header-cell-style="{background:'#68819E',color:'#ffffff'}"
                  height="500px"
                  size="mini"
                  style="width: 100%">
                  <el-table-column
                    prop="img">
                    <!--label="">-->
                    <template slot-scope="scope">
                      <img style="width: 20px;height: 20px"
                           :src="scope.row.img"/><!---->
                      <!--label  -->
                      <!--<span style="margin-left: 10px"></span>-->
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="report"
                    label=""
                    width="280">
                  </el-table-column>
                  <el-table-column
                    prop="count"
                    label="">
                  </el-table-column>
                </el-table>
              </div>
              <div class="mask-right">
                <div class="mask-right-first" style="">
                  <label style="color: black"></label>
                </div>
                <div class="mask-right-second">
                  <div>
                    <input value="" style="width: 80px;border: none;margin-right: 10px;background: #ffffff" disabled readonly/>
                    <el-date-picker
                      v-model="reservearrive"
                      type="datetime"
                      prefix-icon="el-icon-date"
                      placeholder=""
                      size="mini" style="width: 165px;margin-left: -10px">
                    </el-date-picker>
                  </div>
                  <div>
                    <input value="" style="width: 80px;border: none;background: #ffffff" disabled readonly/>
                    <input/>
                  </div>
                  <div>
                    <input value="" style="width: 80px;border: none;background: #ffffff" disabled readonly/>
                    <input/>
                  </div>
                </div>
                <div class="mask-right-three">
                  <label style="color: black"></label>
                </div>
                <div class="mask-right-fifth">
                  <el-radio-group v-model="radioshift">
                    <el-radio :label="3"></el-radio>
                    <el-radio :label="6"></el-radio>
                    <el-radio :label="9"></el-radio>
                  </el-radio-group>
                </div>
                <div class="mask-right-last">
                  <textarea></textarea>
                </div>
              </div>
            </div>
            <div class="mask-bottom">
              <button></button>
              <button @click="setMaskShow" style="background: #68819E"></button>
            </div>
          </div>
        </div>
        <!---->
      </div>
    </el-col>

    <!---->
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </el-row>

</template>

<script>
  export default {
    name: 'home',
    data () {
      return {
        //
        reservearrive:'',
        //
        radioshift:3,
        //
        maskShow:false,
        defaultActiveIndex: "/",
        loading: false,
        nickname: '',
        //
        tableData: [{
          img: [require("@/assets/images/pms/shifting/F.png")],
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: ''
        }, {
          img: '',
          report: 'AR',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: 'AR',
          count: '1'
        }, {
          img: '',
          report: '',
          count: ''
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: ''
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }, {
          img: '',
          report: '',
          count: '1'
        }]
      }
    },
    created() {// 
      //  data  observed 
      this.fetchNavData();
    },
    methods: {
      /**
       * 
       * @param index
       */
      setMaskShow(){
        this.maskShow = !this.maskShow;
      },
      handleSelect(index){
        this.defaultActiveIndex = index;
      },
      fetchNavData () { // 
      // debugger
        var cur_path = this.$route.path; //
        var routers = this.$router.options.routes; // 
        var nav_type = "", nav_name = "";
        for (var i = 0; i < routers.length; i++) {
          var children = routers[i].children;
          if(children){
            for (var j = 0; j < children.length; j++) {
              var grand_children = children[j].children;
              if(grand_children){
                for (var k = 0; k < grand_children.length; k++) {
                  if (grand_children[k].path === cur_path) {
                    nav_type = routers[i].type;
                    nav_name = routers[i].name;
                    break;
                  }
                  // 
                  if(children[j].children) {
                    let grandChildren = children[j].children;
                    for(let z=0; z<grandChildren.length; z++) {
                      if(grandChildren[z].path === cur_path) {
                        nav_type = routers[i].type;
                        nav_name = routers[i].name;
                        break;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        this.$store.state.topNavState = nav_type;  // topNavState
        this.$store.state.leftNavState = nav_name;  // leftNavState
        if(nav_type == "home"){
          this.defaultActiveIndex = "/";
        } else {
          this.defaultActiveIndex = "/" + nav_name + "Manager";
        }
      },
      jumpTo(url){
        this.defaultActiveIndex = url;
        this.$router.push(url); //go
      },
      logout(){
        //logout
        let that = this;
        this.$confirm('?', '', {
          confirmButtonClass: 'el-button--warning'
        }).then(() => {
          //
          localStorage.removeItem('access-user');
          that.$router.go('/login'); //go
        }).catch(() => {});
      }
    },
    mounted() {
      let user = localStorage.getItem('access-user');
      if (user) {
        user = JSON.parse(user);
        this.nickname = user.nickname || '';
      }
    },
    watch: {
      '$route': 'fetchNavData'  //router
    }
  }
</script>
<style lang="less">
  .mask{
    width: 100%;
    height: 100%;
    position: fixed;
    //opacity
    background: rgba(7, 13, 32, 0.2);
    top: 0;
    left: 0;
    z-index: 998;
    //
    .mask-suspen {
      background: #F5F5F5;
      width: 80%;
      height: 80%;
      position: fixed;
      z-index: 999;
      top: 10%;
      left: 10%;
      //
      .mask-head{
        height: 40px;
        background: #303A41;
        line-height: 40px;
        label{
          color: #ffffff;
          margin-left: 10px;
        }
      }
      //table
      .mask-body{
        border-bottom: 1px solid #CCCCCC;
        //table
        .mask-left{
          float: left;
          margin-top: 10px;
          margin-left: 10px;
          margin-bottom: 10px;
          width: 65%;
        }
        //
        .mask-right{
          height: 500px;
          float: left;
          margin-left: 10px;
          margin-top: 10px;
          width: 33%;
          .mask-right-first{
            label{
              margin-left: 20px;
            }
          }
          .mask-right-second{
            border: 1px solid #CCCCCC;
            background: #ffffff;
            margin-top: 10px;
            padding-left: 10px;
            input{
              margin-left: 1px;
              height: 27px;
              width: 175px;
              margin-top: 10px;
              margin-bottom: 10px;
              border: 1px solid #CCCCCC;
              border-radius: 4px;
            }
          }
          .mask-right-three{
            margin-top: 10px;
            label{
              margin-left: 20px;
            }
          }
          .mask-right-fifth{
            height: 86px;
            line-height: 100px;
            margin-top: 10px;
            padding-left: 20px;
            background: #ffffff;
            border: 1px solid #CCCCCC;
          }
          .mask-right-last{
            margin-top: 10px;
            height: 70px;
            textarea{
              width: 100%;
              height: 100%;
              border: 1px solid #CCCCCC;
            }
          }
        }

      }
      //
      .mask-bottom{
        button{
          float: right;
          margin-right: 10px;
          margin-top: 10px;
          height: 27px;
          width: 100px;
          border: none;
          color: #ffffff;
          line-height: 27px;
          background: #4488E9;
          border-radius: 4px;
        }
      }

    }
  }
</style>



