        //
        full(){
          // debugger
          let that=this;
          if(that.group===undefined){
            that.group=3;
          }
          that.$axios({
            url: ""+that.api+"/v1/common/hotel_group/get_info/"+that.group,
            method: "get",
            //headers: {"authorization": that.authorization_token}
          })
            .then(res => {
              if (res.data.message=="success"){
                console.log(res.data);
                that.group_list=res.data.data;
                // that.position=that.group_list.position
                // console.log(that.position);

              }
              else{
                console.log(res.data.message);
              }
            })
            .catch(error => {
              console.log(error);
            });
        },

        //
        amend(){
            this.masking=true;
            this.group_masking=true;

          //
          this.full_name =this.group_list.full_name;//
            this. short_name=this.group_list.short_name;  //
            this. code =this.group_list.code;  //
            //
