        omit(){

          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
            console.log(this.group);
            this.$axios({
              url:`"+that.api+"/v1/common/hotel_group/remove/`+this.group,
              method: "post",
              // params:{},
              data:{

              },
              //headers: {'authorization': this.authorization_token}
            })
              .then(res=>{
                if(res.data.message==="success"){
                  location.reload();  //

                }else {
                  console.log(res.data.message)
                }

              })
              .catch(error=>{
                console.log(error);
              })



          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });


        },

        //log
        handleRemove(file, fileList) {
          console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;


        },
        //log

        //
        save(){
          if (this.radio=="1"){
            this.radio=1
          }else if (this.radio=="2"){
            this.radio=0
          }
          if (this.normal=="1"){
            this.normal=1
          }else if (this.normal=="2"){
            this.normal=0
          }
          this.$axios({
            url:""+this.api+"/v1/common/hotel_group/update/"+this.group,
            method: "post",
            // params:{},
            data:{
              //
              full_name :this.full_name, //
              short_name :this.short_name,   //
              code :this.code, //
              audit:this.radio,//
              desc:this.desc,//
              desc_en:this.desc_en,//
              // 
              office_tel:this.office_tel,//
              country_code:this.country_code,//
              reserve_tel:this.reserve_tel,//
              province:this.province,///
              service_tel:this.service_tel,//
              city:this.city,//
