              address_2:this.address_2,//
            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              if(res.data.message==="success"){
                console.log(res);
                this.masking=false;
                this.full();
              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })
        },
        //
        cancel(){
          this.masking=false;
          //
          this.full_name ='', //
            this.short_name ='',   //
            this.code ='',  //
            this.radio= "1",//
            this.desc='',//
            this.desc_en='',//
            // 
            this.office_tel='',//
            this.country_code='',//
            this.reserve_tel='',//
            this. province='',///
            this.service_tel='',//
            this.city='',//
            this.fax='',//
            //
            this.position_east='',// 
            this.position_west='',// 
            //
            this.normal='1',//
            this.web_site='',//
            this.email='',//
            this.photo='',//
            this.logo='',//log
            this.dns='',//DNS
            this.postal_code='',//
            this.address_1='',//1
            this.address_2=''//
        },

      }
    }
</script>

