            // params:{},
            data:{
              //
              full_name :this.input, //
              short_name :this.hotel_input,   //
              code :this.code, //
              audit:this.radio,//
              desc:this.desc,//
              desc_en:this.desc_en,//
              // 
              office_tel:this.office_tel,//
              country_code:this.country_code,//
              reserve_tel:this.reserve_tel,//
              province:this.province,///
              service_tel:this.service_tel,//
              city:this.city,//
              fax:this.fax,//
              position:this.position_west+','+this.position_east,
              //
              // position_east:'',// 
              // position_west:'',// 

              status:this.normal,//
              web_site:this.web_site,//
              email:this.email,//
              photo:'',//
              logo:'',//log
              dns:this.dns,//DNS
              postal_code:this.postal_code,//
              address_1:this.address_1,//1
              address_2:this.address_2,//
            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              if(res.data.message==="success"){
                console.log(res);
                this.masking=false;
                this.department=false;
                this.hotel();
              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              console.log(error);
            })

        },

        //
        append(data) {
          console.log(data);
          this.masking=false;
           this.department=true;
          // this.department_hotel_id=data.hotel_id;//id
          this.superior_department_id=data.id;//
          this.superior_department=data.name;//
          // this.department_hotel=data.short_name;//
          // this.department_parent_id=data.id;//
          if(data.is_hotel && data.is_hotel==1){
            this.department_hotel_id=data.id;
            this.department_hotel=data.name;//
            this.superior_department_id=null//
            this.superior_department='';//

          }else {
            this.department_hotel_id=data.hotel_id;
            this.department_hotel=data.short_name;//
            this.superior_department_id=data.id;//
            this.superior_department=data.name;//
          }

        },
        //
        hotel_save(){
          this.$axios({
            url:""+this.api+"/v1/common/dept/add",
            method: "post",
            // params:{},
            data:{
              hotel_id:this.department_hotel_id,//
              name:this.department_name,//
              parent_id :this.superior_department_id,//id

              audit:this.radio,//
              desc:this.chinese_name,//
              desc_en:this.english_name,//

            },
            //headers: {'authorization': this.authorization_token}
          })
            .then(res=>{
              if(res.data.message==="success"){
                console.log(res);
                this.masking=false;
                this.department=false;
                this.hotel();

              }else {
                console.log(res.data.message)
              }

            })
            .catch(error=>{
              this.error(res.data.message)
              console.log(error);
            })
        },

        //
        remove(node, data) {
          console.log(node, data);
          const parent = node.parent;
          const children = parent.data.children || parent.data;
          const index = children.findIndex(d => d.id === data.id);
          let id=data.id;
          console.log(id)
          console.log(children);
          if (node.childNodes==""){
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.$message({
                type: 'success',
                message: '!'
              });
              children.splice(index, 1);

              this.$axios({
                url:""+this.api+"/v1/common/hotel/remove/${id}",
                method: "post",
                //headers: {'authorization': this.authorization_token}
              })
                .then(res=>{
                  console.log(res.data.message)
                  if (res.data.message=="success"){
                    //
                    this.hotel();
                  }else{
                    console.log(res.data.message)
                  }
                })
                .catch(error=>{
                  console.log(error);
                })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          }else {
            alert("")
          }

        },

      }

    }
</script>

